import React from 'react'
import { Link } from 'react-router-dom'

const NavBar = () => {
  return (
    <>
        <ul>
            <li><Link to= "/pacientes">Pacientes</Link></li>
            <li><Link to= "odontologos">Odontologos</Link></li>
            <li><Link to="/turnos" >Turnos</Link></li>
        </ul>
    </>
  )
}

export default NavBar