import React, { useEffect, useState } from 'react';
import axios from 'axios';

const Odontologos = () => {
    const [dataOdontologos, setDataOdontologos] = useState()

    const getData = () => {
        axios('http://localhost:8080/odontologos')
        .then((response) => setDataOdontologos(response.data))
        .catch((error) => console.log(error))
        .finally(console.log(dataOdontologos))
      }

    useEffect(() =>

        getData()

    , [])



    return (
        <>
            <table border="1">
            <caption>Odontologos</caption>
                <thead>
                    <tr>
                        <th>Nombre Completo</th>
                        <th>Matricula Profesional</th>
                    </tr>
                    </thead>
                    <tbody>
                    {dataOdontologos && dataOdontologos.map(element => <tr  key={element.id}>
                    <td >{element.nombre + " " + element.apellido}</td>
                    <td >{element.matricula}</td></tr>)}
                </tbody>
            </table>

        </>
    )
}

export default Odontologos;
