
import React, { useEffect, useState } from 'react';
import axios from 'axios';

const Pacientes = () => {

  const [dataPacientes, setDataPacientes] = useState()

  const getData = () => {
    axios('http://localhost:8080/pacientes')
    .then((response) => setDataPacientes(response.data))
    .catch((error) => console.log(error))
    .finally(console.log(dataPacientes))
  }


  useEffect( () => {

    getData()
  }
    

  , [])


    return (
    <>
    <>
            <table border="1">
            <caption>Pacientes</caption>
                <tbody>
                    <tr>
                        <th>Nombre Completo</th>
                        <th>DNI</th>
                        <th>Fecha de Ingreso</th>
                        <th>Email</th>
                        <th>Dirección</th>
                        <th>Ciudad</th>
                        <th>Provincia</th>
                    </tr>
                    {dataPacientes && dataPacientes.map(element => <tr key={element.id}><td>{element.nombre + " " + element.apellido}</td>
                    <td>{element.dni}</td>
                    <td>{element.fechaIngreso}</td>
                    <td>{element.email}</td>
                    <td>{element.domicilio.calle +" " + element.domicilio.numero}</td>
                    <td>{element.domicilio.localidad}</td>
                    <td>{element.domicilio.provincia}</td></tr>)}
                </tbody>
            </table>

        </>
    </>
  )
}

export default Pacientes;
