
import React, { useEffect, useState } from 'react';
import axios from 'axios';

const Turnos = () => {
  const [dataTurnos, setDataTurnos] = useState()

const getData = () => {
  axios('http://localhost:8080/turnos')
  .then((response) => setDataTurnos(response.data))
  .catch((error) => console.log(error))
  .finally(console.log(dataTurnos))
}

  useEffect(() =>
     getData()

  , [])


  return (
    <>
      <>
        <table border="1">
          <caption>Turnos</caption>
          <thead>
          <tr>
              <th colSpan={"1"} rowSpan={"2"}> Fecha</th>
              <th colSpan={"7"}> Paciente</th>
              <th colSpan={"2"}>Odontologo</th>
            </tr>
            <tr>
              <th>Nombre Completo</th>
              <th>DNI</th>
              <th>Fecha de Ingreso</th>
              <th>Email</th>
              <th>Dirección</th>
              <th>Ciudad</th>
              <th>Provincia</th>
              <th>Nombre Completo</th>
              <th>Matricula Profesional</th>
            </tr>
          </thead>
          <tbody>
            {dataTurnos && dataTurnos.map(element => 
            <tr key={element.id} >
            <td>{element.fecha}</td>
            <td>{element.paciente.nombre + " " + element.paciente.apellido}</td>
            <td>{element.paciente.dni}</td>
            <td>{element.paciente.fechaIngreso}</td>
            <td>{element.paciente.email}</td>
            <td>{element.paciente.domicilio.calle + " " + element.paciente.domicilio.numero}</td>
            <td>{element.paciente.domicilio.localidad}</td>
            <td>{element.paciente.domicilio.provincia}</td>
            <td>{element.odontologo.nombre + " " + element.odontologo.apellido}</td>
            <td>{element.odontologo.matricula}</td></tr>)}
          </tbody>
        </table>

      </>
    </>
  )
}

export default Turnos;